.CalculatorDisplay {
    background-color: #444;
    color: #EEE;
    border-radius: 5px;
    margin-bottom: 0.05em;
    height: 3em;

    display: grid;
    grid-template-rows: 1fr 2fr;

    text-align: right;
    padding-left: 0.5em;
    padding-right: 0.5em;

}

@media (max-width: 800px) {
    .CalculatorDisplay {
        width: (80vw + 6px);
      }
  }

.CalculatorDisplay .BreadCrumbs {
    font-size: 0.5em;
    line-height: 2em;
}

.CalculatorDisplay .Display {
    line-height: 2em;
}